/* Burger */

.Header_burger__14yHT {
  display: none;
  position: relative;
  width: 30px;
  height: 18px;
  cursor: pointer;
}

.Header_burger__14yHT span,
.Header_burger__14yHT::before,
.Header_burger__14yHT::after {
  position: absolute;
  left: 0;
  height: 10%;
  width: 100%;
  transition: all 0.3s ease 0s;
  background-color: var(--white);
}

.Header_burger__14yHT::before,
.Header_burger__14yHT::after {
  content: "";
}

.Header_burger__14yHT::before {
  top: 0;
}

.Header_burger__14yHT::after {
  bottom: 0;
}

.Header_burger__14yHT span {
  top: 50%;
  transform: scale(1) translate(0px, -50%);
}

.Header_burger__14yHT.Header__active__NTfLI span {
  transform: scale(0) translate(0px, -50%);
}

.Header_burger__14yHT.Header__active__NTfLI::before {
  top: 50%;
  transform: rotate(-45deg) translate(0px, -50%);
}

.Header_burger__14yHT.Header__active__NTfLI::after {
  bottom: 50%;
  transform: rotate(45deg) translate(0px, 50%);
}

@media (max-width: 767px) {
